    font-family: Comforta;
    src: url(/static/media/Comfortaa-Regular.d350ce24.ttf);
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
  }
  
  .navbar {
    margin-bottom: 30px;
  }
.ico-btn {
  z-index: 999;
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 36px;
  height: 36px;
  cursor: pointer;
  transition: all 0.2s ease-in;
  border-radius: 50%;
  color: white;
}

.ico-btn:hover {
  background-color: rgba(255, 255, 255, 0.14);
}

.ico-btn__burger,
.ico-btn__burger::before,
.ico-btn__burger::after {
  width: 20px;
  height: 2px;
  background: currentColor;
  border-radius: 2px;
  transition: all 0.25s cubic-bezier(0.4, 0, 0.2, 1);
}

.ico-btn__burger::before {
  content: "";
  position: absolute;
  -webkit-transform: translateY(-6px);
          transform: translateY(-6px);
}

.ico-btn__burger::after {
  content: "";
  position: absolute;
  -webkit-transform: translateY(6px);
          transform: translateY(6px);
}

.ico-btn.is-active .ico-btn__burger {
  background: transparent;
}

.ico-btn.is-active .ico-btn__burger::before {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.ico-btn.is-active .ico-btn__burger::after {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.ico-btn.is-active {
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
}

.nav-wrapper {
  position: fixed;
  height: 70px;
  z-index: 9;
  background: rgb(27, 30, 45);
  top: 0;
  right: 0;
  left: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 10px;
}

.nav {
  margin: 30px 0;
  list-style: none;
  display: grid;
  grid-auto-flow: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: start;
          justify-content: start;
  grid-column-gap: 25px;
}

.nav > li {
  font-size: 14px;
  font-weight: 600;
  letter-spacing: 3px;
}

.slider {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: -100px;
  z-index: 888;
  background: rgba(0, 0, 0, 1);
  transition: 0.5s ease-in-out;
  -webkit-transform: translateY(-200vh);
          transform: translateY(-200vh);
}

.active {
  -webkit-transform: translateY(0);
          transform: translateY(0);
}

.burger {
  z-index: 999;
  margin: auto 0 auto 20px;
  cursor: pointer;
}

.list {
  display: grid;
  position: relative;
  margin: 100px auto;
  width: 400px;
}

@media only screen and (max-width: 720px) {
  .list {
    position: absolute;
    width: 80%;
  }
}

@-webkit-keyframes listItemFade {
  from {
    opacity: 0;
    -webkit-transform: translateX(50px);
            transform: translateX(50px);
  }

  to {
    opacity: 1;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}

@keyframes listItemFade {
  from {
    opacity: 0;
    -webkit-transform: translateX(50px);
            transform: translateX(50px);
  }

  to {
    opacity: 1;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}

.list > a {
  opacity: 0;
  color: white;
  padding: 20px 0 20px 0;
  text-transform: capitalize;
  list-style-type: none;
  border-bottom: 0.1px solid rgb(240, 98, 73);
}


.input-container {
  position: relative;
  text-align: left;
  margin: 15px 0;
}

.input-container > label {
  margin: 5px 0px;
  color: aliceblue;
}

.input-container > input {
  width: 100%;
  padding: 10px 40px;
  height: 50px;
  background: #42474b;
  border-bottom-color: rgb(240, 98, 73);
  border-top: 0px;
  border-right: 0px;
  border-left: 0px;
  place-content: rgb(240, 98, 73);
  border-radius: 3px;
  color: white;
}

.input-container > input:focus {
  outline: 0;
}

.input-container > #input:focus {
  outline: none;
}

.input-container > .input-icon {
  position: absolute;
  color: wheat;
  z-index: 2;
  text-align: left;
  top: 48px;
  left: 15px;
}


.btn {
  text-align: center;
  display: block;
  margin: 30px auto;
  color: white;
  padding: 15px 50px;
  border: 0px;
  border-radius: 50px;
  background: linear-gradient(to top, #ff4b2b, #ff416c);
  cursor: pointer;
}

.btn:disabled {
  background: linear-gradient(to top, rgb(184, 65, 44), rgb(189, 59, 89));
  color: lightgray;
}


.header {
  color: white;
  text-align: center;
  font-size: 35px;
  margin: 0 0 20px 0;
}
